body {
  margin: 0;
  padding: 0; 
  color: #333;
  font-family: 'Bai Jamjuree', sans-serif;
  background-color: #f5f5f5;
  min-height: 100vh;
}

::-webkit-input-placeholder {
  color: #999;
  opacity: 1;
}

::-ms-input-placeholder {
  color: #999;
  opacity: 1;
}

::placeholder {
  color: #999;
  opacity: 1;
}
#header {
  background-color: #e9e9e9;
  border-bottom: 2px solid #777;
}

#header #search-form {
  display: flex;
  justify-content: space-between;
}

#header #header-title {
  margin: 0;
  padding: 30px 50px;
  letter-spacing: 1px;
}

#header #search-input {
  color: #333;
  font-size: 18px;
  border-radius: 500px;
  border: 2px solid rgba(58, 58, 58, 0.26);
  width: 50px;
  height: 50px;
  position: absolute;
  right: 43px;
  top: 23px;
  background-color: transparent;
  transition: all .3s;
  cursor: pointer;
}

#header #search-input:focus {
  border: 2px solid #333;
}

#header #search-input::-webkit-input-placeholder {
  color: transparent;
  transition: color .3s;
}

#header #search-input::-ms-input-placeholder {
  color: transparent;
  transition: color .3s;
}

#header #search-input::placeholder {
  color: transparent;
  transition: color .3s;
}

#header #search-input:focus::-webkit-input-placeholder {
  color: #999;
}

#header #search-input:focus::-ms-input-placeholder {
  color: #999;
}

#header #search-input:focus::placeholder {
  color: #999;
}

#header #search-btn {
  border-radius: 500px;
  width: 55px;
  height: 55px;
  position: absolute;
  right: 44px;
  top: 24px;
  background-color: transparent;
  border: none;
  z-index: -10;
}

#header #search-btn:hover {
  cursor: pointer;
}

#header .svgIcon {
  padding: 30px 50px;
}

#header .search-container:hover {
  cursor: pointer;
}

#header .svgIcon path {
  -webkit-transform: scale(2);
          transform: scale(2);
}

@media only screen and (max-width: 800px) {
  #header #search-form {
    flex-direction: column;
  }

  #header #header-title {
    text-align: center;
  }

  #header #search-input {
    position: relative;
    top: auto;
    left: 60px;
    margin: auto;
    margin-bottom: 30px;
    width: 400px;
    cursor: text;
    padding-left: 25px;
    padding-right: 50px;
  }

  #header #search-btn {
    position: absolute;
    right: -16px;
    top: 0;
    z-index: 10;
  }

  #header #search-form > .container {
    margin: auto;
    position: relative;
    right: 40px;
    width: auto;
  }

  #header .svgIcon {
    position: relative;
    left: 10px;
    bottom: 3px;
    padding: 0;
  }
}

@media only screen and (max-width: 530px) {
  #header #search-input {
    width: 350px;
    font-size: 14px;
  }

  #header #search-input::-webkit-input-placeholder {
    font-size: 15px;
  }

  #header #search-input::-ms-input-placeholder {
    font-size: 15px;
  }

  #header #search-input::placeholder {
    font-size: 15px;
  }
}

@media only screen and (max-width: 475px) {
  #header #search-input {
    width: 250px;
  }
}

@media only screen and (max-width: 385px) {
  #header #search-input {
    width: 200px;
  }
}
.error-container {
  text-align: center;
}

.error {
  background-color: rgba(255, 122, 122, 0.4);
  padding: 20px 40px;
  border-radius: 5px;
  font-size: 15px;
  margin: auto;
  text-align: center;
  display: inline-block;
}
#switch-container {
	width: 190px;
	height: 36px;
	margin: auto;
	position: relative;
  top: 20px;
	border-radius: 6px;
	overflow: hidden;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	cursor: pointer;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

#switch-container .inner-container {
	position: absolute;
	left: 0;
	top: 0;
	width: inherit;
	height: inherit;
	text-transform: uppercase;
  font-size: 12px;
  font-weight: bold;
	letter-spacing: .2em;
}

#switch-container .inner-container:first-child {
	background: #e9e9e9;
	color: #a9a9a9;
}

#switch-container .inner-container:nth-child(2) {
	background: dodgerblue;
	color: white;
	-webkit-clip-path: inset(0 50% 0 0);
	        clip-path: inset(0 50% 0 0);
	transition: .3s cubic-bezier(0,0,0,1);
}

#switch-container .toggle {
	width: 50%;
	position: absolute;
	height: inherit;
	display: flex;
	box-sizing: border-box;
}

#switch-container .toggle p {
	margin: auto;
}

#switch-container .toggle:nth-child(1) {
	right: 0;
}
.card {
  background-color: #f1f1f1;
  border: 2px solid #ccc;
  border-radius: 5px;
  margin: 40px 20px;
  width: 300px;
  height: 275px;
  transition: all .3s;
}

.card:hover {
  box-shadow: -10px 10px 30px 0px rgba(131, 131, 131, 0.7);
}

.card .day-name {
  text-align: center;
  margin-bottom: -5px;
  margin-top: 4px;
}

.card .card-img {
  display: block;
  margin: auto;
}

.card .temp-range {
  text-align: center;
  margin-top: -10px;
  font-size: 22px;
}

.card .weather-info {
  margin: 0 15px;
}

.card .weather-info > div {
  display: flex;
  justify-content: space-between;
  padding: 2px 0;
}
#weather-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 20px;
}
#main {
  margin-top: 40px;
}

#main h2.location {
  text-align: center;
  font-size: 27px;
  margin-top: 40px;
}

#main .flex-container {
  display: flex;
  justify-content: center;
}

#main .flex-container > * {
  width: 40%;
  margin: 40px;
}

@media only screen and (max-width: 1415px) {
  #main .flex-container {
    flex-direction: column;
  }

  #main .flex-container > * {
    width: 80%;
    height: 400px;
    margin: auto;
  }

  #main .flex-container .chart-container {
    margin-bottom: 170px;
  }

  #main .flex-container #map {
    margin-bottom: 50px;
  }
}

@media only screen and (max-width: 1100px) {
  #main .flex-container .chart-container {
    margin-bottom: 50px;
  }
}

@media only screen and (max-width: 750px) {
  #main .flex-container .chart-container {
    margin-bottom: -15px;
  }
}

@media only screen and (max-width: 550px) {

  #main .flex-container .chart-container {
    width: 95%;
    margin-bottom: -105px;
  }
}

@media only screen and (max-width: 450px) {
  #main .flex-container .chart-container {
    margin-bottom: -200px;
  }
}
#footer {
  display: flex;
  justify-content: space-between;
  padding: 10px 25px;
  background-color: #e9e9e9;
}

#footer a {
  color: #666;
  transition: color .2s;
}

#footer a:hover {
  color: #000;
}

@media only screen and (max-width: 500px) {
  #footer {
    flex-direction: column;
    text-align: center;
  }
}

.loading-overlay {
  background-color: #f5f5f5;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
}

.loading-container {
  width: 200px;
  height: 200px;
  border: 10px solid #ff6600;
  border-top: 10px solid #185ab1;
  border-bottom: 10px solid #185ab1;
  border-radius: 50%;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-animation: rotate 2s infinite ease-in-out;
          animation: rotate 2s infinite ease-in-out;
}

@-webkit-keyframes rotate {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
            transform: translate(-50%, -50%) rotate(0deg);
  }

  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
            transform: translate(-50%, -50%) rotate(360deg);
  }
}

@keyframes rotate {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
            transform: translate(-50%, -50%) rotate(0deg);
  }

  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
            transform: translate(-50%, -50%) rotate(360deg);
  }
}
