#header {
  background-color: #e9e9e9;
  border-bottom: 2px solid #777;
}

#header #search-form {
  display: flex;
  justify-content: space-between;
}

#header #header-title {
  margin: 0;
  padding: 30px 50px;
  letter-spacing: 1px;
}

#header #search-input {
  color: #333;
  font-size: 18px;
  border-radius: 500px;
  border: 2px solid rgba(58, 58, 58, 0.26);
  width: 50px;
  height: 50px;
  position: absolute;
  right: 43px;
  top: 23px;
  background-color: transparent;
  transition: all .3s;
  cursor: pointer;
}

#header #search-input:focus {
  border: 2px solid #333;
}

#header #search-input::placeholder {
  color: transparent;
  transition: color .3s;
}

#header #search-input:focus::placeholder {
  color: #999;
}

#header #search-btn {
  border-radius: 500px;
  width: 55px;
  height: 55px;
  position: absolute;
  right: 44px;
  top: 24px;
  background-color: transparent;
  border: none;
  z-index: -10;
}

#header #search-btn:hover {
  cursor: pointer;
}

#header .svgIcon {
  padding: 30px 50px;
}

#header .search-container:hover {
  cursor: pointer;
}

#header .svgIcon path {
  transform: scale(2);
}

@media only screen and (max-width: 800px) {
  #header #search-form {
    flex-direction: column;
  }

  #header #header-title {
    text-align: center;
  }

  #header #search-input {
    position: relative;
    top: auto;
    left: 60px;
    margin: auto;
    margin-bottom: 30px;
    width: 400px;
    cursor: text;
    padding-left: 25px;
    padding-right: 50px;
  }

  #header #search-btn {
    position: absolute;
    right: -16px;
    top: 0;
    z-index: 10;
  }

  #header #search-form > .container {
    margin: auto;
    position: relative;
    right: 40px;
    width: auto;
  }

  #header .svgIcon {
    position: relative;
    left: 10px;
    bottom: 3px;
    padding: 0;
  }
}

@media only screen and (max-width: 530px) {
  #header #search-input {
    width: 350px;
    font-size: 14px;
  }

  #header #search-input::placeholder {
    font-size: 15px;
  }
}

@media only screen and (max-width: 475px) {
  #header #search-input {
    width: 250px;
  }
}

@media only screen and (max-width: 385px) {
  #header #search-input {
    width: 200px;
  }
}